<p>
One day, they notice a village girl who brings her cow to graze in the same field. After a while, the girl starts talking to them and even shares some milk with them. They become friends, and soon the soldiers look forward to seeing her every Sunday. Luc and Jean even bring her sweets, and their small friendship becomes something they treasure a lot.
</p><p>
