        <p>= Deception enters into their friendship because Luc begins to secretly leave the barracks during the week to meet the village girl without telling Jean, his close friend. At first, both Luc and Jean shared a strong bond and spent Sundays together in the countryside, enjoying the simple pleasure of watching the girl and talking to her. However, when Luc starts visiting the girl alone, he breaks the unspoken trust between them. He even borrows money from Jean without explaining the reason. This secretive behavior shows that Luc is hiding something, and it creates distance and emotional pain for Jean, ultimately damaging their friendship.</p>
